/** @format */

.Type1_Account {
  margin: 25px -30px 0 -58px;
}

.Type1_Note {
  background-color: #f6f9fb;
  font-family: Roboto;
  padding: 25px 25px 70px 25px;
}

.Type1_Note img {
  display: block;
  margin: auto;
}

.Type1_Note p {
  width: 70%;
  margin: 20px auto;
  opacity: 0.5;
  font-size: 16px;
  text-align: center;
  color: #24272c;
}

.Type1_List {
  position: relative;
  margin: -15% auto auto auto;
  width: 85%;
  border-radius: 6px;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.1);
  padding: 35px 25px 35px 25px;
  background-color: #ffffff;
}

.Type1_List p {
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
  text-align: left;
  color: #000000;
}

.Type1_List ul {
  margin-top: 20px;
  list-style: none;
}
.Type1_List li img {
  width: 12px;
  margin-right: 7px;
}

.Type1_List li {
  font-size: 13px;
  font-family: Roboto;
  margin: 10px 10px 10px 0;
}

.Done_Linking {
  font-weight: bold;
  margin-top: 30px;
  font-family: Roboto;
  text-align: center;
  color: #000;
}

.Done_Linking span {
  font-weight: normal;
  text-decoration: underline;
  color: #12b0ee;
}

.Page_Profile {
  margin-bottom: 20px;
  text-align: center;
}

.Page_Profile {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #24272c;
}
.Page_Profile label {
  vertical-align: middle;
  border: 1px solid rgba(36, 39, 44, 0.1);
  border-radius: 8px;
  width: 35px;
  display: inline-block;
  margin-right: 6px;
}

.Page_Profile label img {
  width: 100%;
  border-radius: 3px;
}

.Type1_Button {
  margin-left: 0;
  margin-top: 25px;
}

p.skipBtn {
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 15px;
  text-align: right;
  text-decoration-line: underline;
  color: #24272c;
  mix-blend-mode: normal;
  opacity: 0.7;
  text-align: center;
  margin-top: 30px;
}

p.skipBtn img {
  vertical-align: middle;
  width: 2%;
  margin-left: 5px;
  display: initial;
  margin-top: 0;
  margin-bottom: 0;
}
