/** @format */

section.Instagram {
  width: 100%;
  height: 100vh;
  padding-top: 20px;
  font-family: Roboto, "sans-serif";
}

.link_social {
  margin: 0px 0px 10px 10%;
  opacity: 0.7;
  font-size: 15px;
  color: #2e3239;
}

.Icons {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.Link_Progress {
  position: relative;
  grid-template-columns: 1fr 1fr 1fr;
  padding-left: 10%;
}

.Link_Progress .Line {
  position: absolute;
  top: 50%;
  left: 0;
  width: 27%;
  height: 3px;
  background: #12b0ee;
}

.Skip_Button {
  align-self: center;
}

.Skip_Button a {
  text-decoration: underline;
  opacity: 0.7;
  font-family: Roboto;
  font-size: 13px;
  color: #24272c;
}

.Icon_Grid .Icon {
  width: 40px;
  border-radius: 3px;
  z-index: 1;
}

.Link_Progress .Icon {
  mix-blend-mode: luminosity;
  opacity: 0.5;
}

.Icon_Grid {
  display: grid;
  grid-template-columns: 70% 30%;
}

.Icon_Grid .Icon:nth-child(1),
.Icon_Grid .Icon:nth-child(2) {
  mix-blend-mode: unset !important;
  opacity: 1 !important;
}

.Icon_Grid .Icon:nth-child(2) {
  border: 4px solid #12b0ee;
  border-radius: 10px;
  padding: 1px;
  box-shadow: 0 -1px 5px 0 rgba(0, 0, 0, 0.15);
}

.Form_Header {
  padding-top: 42px;
  display: grid;
  grid-template-columns: 15% 80%;
}

.Form_Header .Back_Btn {
  margin-top: 5px;
  align-self: baseline;
  text-align: center;
}

.Header_Text h1 {
  text-align: start;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
  letter-spacing: normal;
  color: #3b414a;
}

.Page_Profile {
  background-color: #f6f9fb;
  display: grid;
  grid-template-columns: 80% 10%;
  padding: 4% 10% 4% 10%;
  margin: 30px auto;
  align-items: flex-start;
}

.Page_Profile a {
  text-decoration: underline;
  opacity: 0.7;
  font-size: 13px;
  color: #24272c;
  margin-top: 14px;
}
.Page_Profile > label {
  display: grid;
  grid-template-columns: 22% 65%;
}

.Page_Profile h1 {
  font-family: Roboto;
  font-size: 18px;
  font-weight: 500;
  color: #24272c;
  align-self: center;
}

.Page_Profile input {
  width: 22px;
  height: 25px;
}

.PageImg {
  width: 35px;
  border-radius: 3px;
}
.Form_Text {
  width: 70%;
  margin: 30px auto 0px;
}
.Form_Text h1 {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.3;
  color: #434a54;
}

.Form_Text p {
  line-height: 2;
  opacity: 0.7;
  font-size: 14px;
  color: #2e3239;
}

.UserConfirmation {
  grid-template-columns: 10% 90%;
}

.UserConfirmation label {
  opacity: 0.7;
  font-size: 15px;
  color: #2e3239;
}

.UserConfirmation label img {
  margin-left: 10px;
  width: 12px;
}

.UserConfirmation label a {
  color: black;
  text-decoration: underline;
}

.UserConfirmation input {
  height: 20px;
  width: 20px;
}

.Connection_Info {
  width: 90%;
  margin: auto;
}

.Connection_Info h3 {
  font-size: 16px;
}

.Connection_Info p {
  font-family: Roboto;
  font-weight: bold;
  font-size: 11px;
  color: #2c2c2c;
  text-align: left;
  letter-spacing: 1px;
}
.Connection_Info button {
  padding: 7px;
  border-radius: 4px;
  background-color: #1877f2;
  width: 90%;
  /* margin: auto; */
}
.Page_label {
  transform: none;
  position: relative;
  top: 0;
}
